  position: fixed;
  right: 32px;
  bottom: 32px;
  z-index: 10;
  display: flex;
  align-items: center;
  input {
    border-bottom-left-radius: 20px;
    border-top-left-radius: 20px;
    border: 2px solid grey;
    border-right: 0;
    height: 32px;
    margin-right: -10px;
    padding: 4px 10px;
    width: 320px;
    display: none;
    // animation: mymove infinite;
    // animation-duration: 1s;
    // @keyframes mymove {
    //   from {
    //     width: 0px;
    //   }
    //   to {
    //     width: 320px;
    //   }
    // }
  }
  input:focus {
    outline: none;
  }
  button {
    background: url("/static/mask.png");
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    background-color: white;
    height: 64px;
    width: 64px;
    border-radius: 50%;
    border: 5px solid #ed4134;
  }
  button:focus {
    outline: none;
    background-color: white;
  }
}
